.barChartRace {
  position: relative;
}
.barChartRace input[type=range] {
  -webkit-appearance: none;
  width: 6rem;
  border-radius: 0.8rem;
}
.barChartRace input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 0.07rem;
  width: 0.07rem;
  margin-top: -0.0275rem;
  background: #3DC1D4;
  border-radius: 50%;
}
.barChartRace input[type=range]::-webkit-slider-runnable-track {
  height: 0.026042rem;
  background: #F2F2F2;
  border-radius: 0.266667rem;
}
.barChartRace input[type=range]:focus {
  outline: none;
}
.barChartRace .scroll {
  display: flex;
  color: black;
}
.barChartRace .scroll .icon {
  align-items: center;
  height: 0.146667rem;
  width: 0.366667rem;
}
.barChartRace .scroll .w_bottom_operation {
  height: 0.146667rem;
  line-height: 0.146667rem;
  width: calc(100%);
}
.barChartRace .scroll .w_bottom_operation .progress {
  width: calc(100% - 0.533333rem) !important;
}
.barChartRace .domain {
  display: none;
}
.barChartRace .tick line {
  stroke: #2A2A30;
}
.barChartRace .tick text {
  fill: #8E8883;
  color: rgba(255, 255, 255, 0.38);
  font-size: 0.293333rem;
  font-family: "Microsoft YaHei";
}
.barChartRace .tick-dy text {
  fill: #8E8883;
  font-size: 10pt;
  font-family: "Microsoft YaHei";
}
.barChartRace .label {
  fill: black;
  opacity: 0.9;
  font-weight: 500;
  font-size: 0.0855rem;
  font-family: "Microsoft YaHei";
  font-weight: bold ;
  width: 100px !important;
  text-overflow: ellipsis;
}
.barChartRace .dateLabel {
  color: black;
