// 大div
.jinrong-dashboard {
  display: flex;
  height: 3.28125rem;
  // background-color: #fff;
  background-image: url('./images/background.jpg');
  background-size: cover;

  >div {
    height: 100%;
  }

  // box容器
  .boxContainer {
    padding: .058594rem 0 0 .058594rem; //45
    position: relative;

    &::before {
      position: absolute;
      content: '';
      width: .071615rem; //55
      height: .071615rem; //55
      border-top: .010417rem solid #00F4FF; //8
      border-left: .010417rem solid #00F4FF; //8
      border-radius: .002604rem; //2
    }

    &::after {
      position: absolute;
      right: 0;
      top: .058594rem;
      content: '';
      width: .071615rem; //55
      height: .071615rem; //55
      border-top: .010417rem solid #00F4FF;
      border-right: .010417rem solid #00F4FF;
      border-radius: .002604rem; //2
    }

    .box_footer {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }

    .box_footer::before {
      position: absolute;
      left: .058594rem;
      bottom: 0;
      content: '';
      width: .071615rem; //55
      height: .071615rem; //55
      border-bottom: .010417rem solid #00F4FF;
      border-left: .010417rem solid #00F4FF;
      border-radius: .002604rem; //2
    }

    .box_footer::after {
      position: absolute;
      bottom: 0;
      right: 0;
      content: '';
      width: .071615rem; //55
      height: .071615rem; //55
      border-bottom: .010417rem solid #00F4FF;
      border-right: .010417rem solid #00F4FF;
      border-radius: .002604rem; //2
    }

    //每一个box
    .box {
      height: 100%;
      border: 1px solid #59B4FF;
      border-radius: 1px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
    }

  }

  //左边
  .left {
    // background-color: #1A387D;
    width: 26%;
    display: flex;
    flex-direction: column;
    padding-bottom: .054688rem; //30

    // 地图div
    >div:first-child {
      height: 1.154948rem; //887
      width: 2.513021rem; //1930

      #map {
        width: 100%;
        height: 100%;
        position: relative;
        // padding:.010417rem; //8
      }

      .mapLabel {
        position: absolute;
        width: 85%;
        height: 85%;
        left: .188802rem; //140
        bottom: .184896rem; //142
      }

      // #mySvg {
      //   position: absolute;
      //   width: 85%;
      //   height: 85%;
      //   left: .188802rem; //140
      //   bottom: .184896rem; //142
      // }

    }

    // 表格div
    >div:nth-child(2) {
      // height: 1.957031rem; //1503
      width: 2.513021rem; //1930
      flex-grow: 1;

    }

  }

  .center {
    height: 100%;
    // align-self: flex-end;
    flex-grow: 1;
    width: 47.2%;
    left: 26.5%;


    >div:first-child {
      color: #41FDFF;
      font-size: .0625rem; //48
      letter-spacing: .008464rem; //6.5
      line-height: .08724rem; //67
      height: 13.7%;
      background-image: url('./images/hearder.svg');
      background-size: 100%;
      position: relative;

      >div:first-child {
        position: absolute;
        left: .264323rem; //203
        top: .138021rem; //106
      }

      >div:last-child {
        position: absolute;
        right: .3125rem; //240
        top: .138021rem; //106
      }

      >div:nth-child(2) {
        font-size: .117188rem; //90
        line-height: .164063rem; //126
        letter-spacing: .017708rem; //13.6
        position: absolute;
        left: 1.347656rem; //1035
        top: .138021rem; //106
      }
    }

    >div:last-child {
      width: 100%;
      height: 86.2%;

      >div:first-child {
        // width: 100%;
        // height: 100%;
        text-align: center;

        /* >object {
          position: relative;
          top: 5%;
          width: 88%;
          height: 88%;
        } */
        position: relative;
        top: 5%;
        left: 4.2%;
        width: 88%;
        height: 88%;

      }
    }

  }

  // 右部分
  .right {
    // background-color: #1A387D;
    width: 25.8%;

    //右边唯一一个div
    >div:first-child {
      height: 3.166667rem; //2432
      width: 2.494792rem; //1916
      margin: .058594rem .05599rem .05599rem .028646rem;

      .right-title {
        margin-top: .10612rem; //81.5
        width: .720052rem; //553
        color: #41FDFF;
        position: relative;
      }

      .right-title::before {
        left: -29%;
        top: 45%;
      }

      .right-title::after {
        right: -29%;
        top: 45%;
      }

      // tab标签  热门、推荐
      .right-tab-title {
        color: #CBCBCB;
        font-size: .052083rem; //40
        display: flex;
        text-align: center;
        vertical-align: middle;
        line-height: .143229rem;
      }

      .right-tab-title:hover {
        cursor: pointer;
      }

      .frist {
        position: absolute;
        top: .459635rem; //353
        left: .207031rem; //159
        width: .46875rem; //360
        height: .143229rem; //110
        background-size: cover;
      }

      // 名字起错了
      .frist-select {
        background-image: url('./images/popular.svg');
      }

      .frist-unselect {
        background-image: url('./images/popular_select.svg');
      }

      .second {
        position: absolute;
        top: .459635rem; //353
        left: .623698rem; //479
        width: .414063rem; //318
        height: .143229rem; //110
        background-size: cover;
      }

      .second-select {
        background-image: url('./images/recommend_select.svg');
      }

      .second-unselect {
        background-image: url('./images/recommend.svg');
      }

      // 新闻div
      .right-tab-div {
        position: absolute;
        top: .601563rem; //462
        // left: .205729rem;
        width: 2.135417rem; //1640
        height: 2.346354rem; //1200
        max-height: 2.346354rem; //1200
        border: .010417rem solid #175592; //8
        background-color: #092A67;
        color: #41FDFF;
        display: flex;
        flex-direction: column;

        // 每个新闻标题一个div
        >div {
          font-size: .026041rem; //20
          line-height: .117187rem; //90
          width: 100%;
          height: .117187rem; //90
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: space-between;
          align-items: center;

          // 新闻标题div
          >div:first-child {
            margin-left: 8%;
            width: 60%;
            height: 100%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }

          //时间div
          >div:last-child {
            margin-right: .157552rem; //121
          }
        }

        // 占位div
        >div:first-child,
        >div:last-child {
          height: .15625rem; //120
          visibility: hidden;
        }
      }
    }
  }
}

// div标题
.domestic-stock-title {
  margin-top: .054036rem; //41.5
  height: .08724rem; //67
  width: .576823rem; //443
  line-height: .08724rem; //67
  text-align: center;
  color: #41FDFF;
  font-family: "Microsoft YaHei", Arial, Helvetica, sans-serif;
  font-size: .0625rem; //48
  letter-spacing: .009505rem; //7.3
  // font-weight: 400;
  position: relative;
  // top: .104167rem; //40
}

.domestic-stock-title::before {
  position: absolute;
  width: .091146rem; //70
  height: .020833rem; //16
  left: -35%;
  top: 45%;
  content: '';
  display: block;
  background: url('./images/titleleft.svg') no-repeat;
  background-size: 100%;
}

.domestic-stock-title::after {
  position: absolute;
  width: .091146rem; //70
  height: .020833rem; //16
  right: -35%;
  top: 45%;
  content: '';
  display: block;
  background: url('./images/titleright.svg') no-repeat;
  background-size: 100%;
}

// 表格
.stock-table {
  width: 2.302083rem; //1768
  font-size: .052083rem; //40
  text-align: right;
  border-collapse: collapse;
  line-height: .072917rem; //56
  letter-spacing: 0;
  margin-top: .052734rem; //40.5

  th {
    color: #FFFFFF;
    width: .392708rem; //301.6
    height: .104167rem; //80                      
    background-color: #444774;
  }

  td {
    color: #FA2832;
    ;
    width: .392708rem; //301.6
    height: .104167rem; //80
    background-color: #031120;
    border-bottom: .005208rem solid #112D6D; // todo 4px 
  }

  .left-table-firstlast-column {
    width: .104167rem; //80
  }

  .left-table-second-column {
    width: .429688rem; //400
    color: #FFFFFF;
    text-align: left;
  }

  .green-font {
    color: #16BC50;
  }
}